import React, { useState, useEffect, useCallback } from 'react';
import { Box, Grid } from '@mui/material';
import Button from '@mui/material/Button';


import { Title } from '../elements/Title';
import { Text } from '../elements/Text';
import { SubTitle } from '../elements/SubTitle';
import { TextInput } from '../../../uciTablet/components/formulario/helpers/TextInput';
import { TextInputOpt } from '../../../uciTablet/components/formulario/helpers/TextInputOpt';
import { resourceServer } from '../../env';

import { addToken } from '../helpers/token';
import { getImage } from './helpers/getImage';
import { loadPatient } from '../../../uciTablet/recursosFHIR/helpers/loadPatient';
import { verifyFields } from './helpers/verifyFields';
import { imageDefault } from '../../../uciTablet/recursosFHIR/helpers/imageDefault';


const CreatePatient = () => {

    const { urlServer, requestOptionsPost, requestOptionsGet} = resourceServer ;

    const patients = JSON.parse(localStorage.getItem('patients'));
    const units = JSON.parse(localStorage.getItem('units'));

    const [dirfoto, setDirfoto] = useState('');
    const [status, setStatus] = useState('');

    const [ala, setAla] = useState(units[0].ala);
    const [sala, setSala] = useState(units.filter(unit => unit.ala === ala)[0].salas[0].sala);

    
    const getCamas = useCallback( (cama) => {

        const ocupadas = patients.filter(pat => pat.ala === ala).map(pat => parseInt(pat.cama));
        const cap = units.filter(unit => unit.ala === ala)[0].salas[0].capacidad;

        const camas = [...Array(parseInt(cap))].reduce((acc, curr, idx) => {
            if (ocupadas.findIndex(num => num === idx) < 0) {
                acc.push(idx);
            };
            return acc
        }, []);

        console.log( cama, camas[0] );

        return ({
            cama: cama ? cama : camas[0],
            camas,
            ocupadas: ocupadas
        })

    },[ala])

    const [camas, setCamas] = useState( getCamas(null) );


    useEffect(() => {

        setSala(units.filter(unit => unit.ala === ala)[0].salas[0].sala);

        setCamas(getCamas());

    }, [ala, getCamas]);

    useEffect(()=>{
        if( ! localStorage.getItem('Data')  ){
            
            localStorage.setItem('Data', JSON.stringify({Foto:imageDefault}));
            console.log( "Objeto creado");
        }
    },[]);


    const handleButtomFoto = async (e) => {

        if (!e.target.files){
            return;
        };
        
        const data = JSON.parse(localStorage.getItem('Data'));
        const foto =  await getImage([...e.target.files]) ;
        data.Foto = foto.split(',')[1] ;

        localStorage.setItem('Data', JSON.stringify(data));

        setDirfoto(  e.target.files[0].name );
    };


    const handleSend = async() => {

        setStatus( "" );

        const data = JSON.parse(localStorage.getItem('Data'));

        data.Edad = data['Fecha de nacimiento'];

        if(  verifyFields( setStatus, data) ){

            let resPat = await fetch(`${urlServer}Patient/?identifier=${data['Cuenta corriente']}`, addToken(requestOptionsGet) )
            resPat = await resPat.json()
               
            if( resPat.total === 0 ){
                
                requestOptionsPost.body = JSON.stringify( loadPatient(data, true) )
                fetch(`${urlServer}Patient`, addToken(requestOptionsPost, "application/json" ))
                        .then( resp => {  
                            console.log( resp );
                            if( resp.statusText === 'Created'){
                                setStatus( "Paciente creado")
                            }else if( resp.statusText === 'Payload Too Large'){
                                setStatus( "La imagen es muy grande." );
                            }else{
                                setStatus(resp.statusText);
                            }
                            resp.json() 
                        })
                        .then( obj => console.log( obj ))
                        .catch( err => console.log( "Error en la creacion del paciente", err ));
    
            }else{
                setStatus("La cuenta corriente ya existe");
            }
        } ;
    };

    const styleSend = {
        marginTop: '1rem',
        textAlign: 'center'
    };

    return (
        <Box style={{ fontFamily: 'Montserrat' }} >

            <div style={{ background: '#0d47a1', marginBottom: '0.5rem' }}>
                <Title color='white'>
                    Crear paciente
                </Title>
            </div>

            <div style={{ background: 'white', padding: '0.5rem', paddingBottom: '2rem' }}>

                <SubTitle>
                    Información básica del paciente:
                </SubTitle>

                <Grid
                    container
                    direction="row"
                    justifyContent="flex-start"
                    alignItems="flex-start"
                    mt={2}
                >
                    <Grid item xs={6}>

                        <TextInput field='Nombres y apellidos' width={50} />
                        <TextInput field='Cuenta corriente' width={50} />

                        <TextInput field='Sexo' width={30} />


                        <TextInput field='Fecha de nacimiento' type='date' width={50} style={{ width: '267px' }} />

                        <Button
                            variant="outlined"
                            component="label"
                            style={{ marginRight: '0.5rem', border: '2px solid #c4c4c4' }}
                            
                            >
                            <Text bold={'600'}>
                                Cargar foto
                            </Text>
                            <input
                                id='foto'
                                hidden
                                type="file"
                                accept="image/*"
                                onChange={(e) => handleButtomFoto(e)}
                                />
                        </Button>
                        <label>
                            {dirfoto}
                        </label>

                    </Grid>

                    <Grid item xs={6}>

                        <TextInput field='Estado de bienestar (0-3)' width={50} />

                        {
                            [
                                ['Ala', units.map(unit => unit.ala), (arg) => setAla(arg), {Ala:ala} ],
                                ['Sala', [sala], (arg) => setSala( units.filter(unit => unit.ala === ala)[0].salas[0].sala ), {Sala:sala} ],
                                ['Cama', camas.camas, (arg) => setCamas( getCamas(arg) ), {Cama:camas.cama} ]
                            ].map((item, idx) =>
                                    <TextInputOpt
                                        key={idx}
                                        field={item[0]}
                                        opt={item[1]}
                                        blur={(e) => item[2](e.target.innerText)}
                                        dataOut={item[3]}
                                        width={30}
                                    />
                                )
                        }

                    </Grid>

                </Grid>


                <div style={styleSend}>

                    <hr width={'100%'} align="center" size='4' noshade="noshade" color="#c4c4c4" />
                    <br />

                    <Button
                        variant="contained"
                        component="label"
                        style={{ marginRight: '0.5rem', fontWeight: '600' }}
                        onClick={() => handleSend()}
                    >
                        <Text bold={'600'}>
                            Crear
                        </Text>

                    </Button>
                    <label>
                        {status}
                    </label>
                </div>

            </div>

        </Box>
    )
};

export default CreatePatient;








