import React from 'react' ;
// import { makeStyles } from '@material-ui/core' ;
import { MediaQuery } from '../../MediaQuery';
import {colorList} from '../../../../newTheme.js'
import { styled } from '@mui/system';


export const Text = ( props ) => {
     
     const { _xl, _lg } = MediaQuery() ;
     
     const colors = colorList;
     
     const StyledSpan = styled('span')({
          fontWeight: props.bold ? props.bold : '500',
          fontFamily: 'Montserrat !important',
          paddingRight: '0rem !important',
          paddingLeft: '0rem !important',
          marginTop: '0 !important',
          marginBottom: '0 !important',
     });
     
     const StyledP = styled('p')({
          fontWeight: props.bold ? props.bold : '500',
          fontFamily: 'Montserrat !important',
          paddingRight: '0rem !important',
          paddingLeft: '0rem !important'
     });
     // const useStyle = makeStyles( theme => ({
     //      style : {
     //           fontWeight: '500',
     //           fontFamily: 'Montserrat !important',
     //           fontSize: _xl ? '22px' : _lg ? '16px': '12px',
     //           lineHeight: _xl ? '1.5rem' : _lg ? '1.3rem': '0.8rem',
     //           paddingRight: '0rem !important',
     //           paddingLeft: '0rem !important',
     //           marginTop: '0 !important',
     //           marginBottom: '0 !important',
     //           // color: props.color === 'blue' ? colors.color1.main : props.color
     //      }
     // })) ;
     //
     // const classes = useStyle();

     if( props.element ){
          return(
               <StyledP
                    style={{
                         color: props.color === 'blue' ? colors.color1.main : props.color,
                         fontSize: _xl ? '22px' : _lg ? '16px': '12px',
                         lineHeight: _xl ? '1.3rem' : _lg ? '1.1rem': '0.8rem',
                         marginBlock:_xl? '0.2rem' : _lg ? '0.2rem': '0.1rem'
                    }}
               >
                    {props.children}
               </StyledP>
          );
     };

     return(

          <StyledSpan
               style={{
                    ...props.style,
                    color: props.color === 'blue' ? colors.color1.main : props.color,
                    fontSize: _xl ? '22px' : _lg ? '16px': '12px',
                    lineHeight: _xl ? '1.5rem' : _lg ? '1.1rem': '0.8rem !important',
               }}
          >
               {props.children}
          </StyledSpan>
     )
}
