


export const verifyFields = (setMsn, data) => {

    const labels = [   
        'Nombres y apellidos',
        'Cuenta corriente',
        'Sexo',
        'Fecha de nacimiento',
        'Estado de bienestar (0-3)',
        'Ala',
        'Sala',
        'Cama'
    ]; 

    for(let label of labels){
        if( (! data[label]) & (data[label] !== '') ){
            setMsn( `El valor: ${label} es necesario.`);
            return false
        }
    }

    return true

};